import { Component, OnInit } from '@angular/core';

import { finalize } from 'rxjs/operators';

import { APIService } from '../services/api/api.service';
import { Painting } from '../paintings/painting';
import { Utils } from '../utils/utils';

@Component({
  selector: 'paintings',
  templateUrl: './paintings.component.html',
  styleUrls: ['./paintings.component.scss']
})
export class PaintingsComponent implements OnInit {
    loaded: boolean = false;
    paintings: Painting[];
    dim = Math.min(window.innerWidth * 0.9, 800);

    constructor(private _api: APIService) { }

    ngOnInit() {
        this.fetchPaintings()
    }

    fetchPaintings = () => {
        this.loaded = false;

        this._api.getPaintings()
            .pipe(finalize(() => this.loaded = true))
            .subscribe(
                res => this.populatePaintings(res),
                err => console.log(err))
    }

    populatePaintings = (results) => {
        this.paintings = results.map(res => {
            res.data = Utils.rowsToBin(res.data);
            return res;
        });
    }
}
